@import '../../css/colors.scss';



.back {
    width: 70px;
}

.img-container {
    margin: 25px auto;
    width: 80%;

}

.title-actions-container {
    justify-content: space-between;
    margin: 15px 30px;
    flex-wrap: wrap;
}
.actions-container {
    align-self: flex-end;
}
.main-info-container {
    margin: 0 0 30px 30px;
    max-width: 400px;
    min-width: 300px;
    flex-wrap: wrap;
    .fa, .far, .fas {
        font-size: 1.4rem;
    }
}

.organizer-img {
    margin-left: -10px;
    border-radius: 50%;
    height: 40px;
    width: 40px;
    cursor: pointer;
}
.creator-container {
    width: 200px;
}

.more-info-container {
    margin: 15px 30px;
}

.organizer-name {
    display: block;

}

.trip-details {
    background-color: $white-trans;
    margin: 20px;
}
.itinerary-container {
    margin: 0 30px;
    p {
        margin: 7px 0 0 20px;
    }
}


.map-wrapper-details {
    height: 100vh;
    width: 100%;
    margin-top:10px;
    
}
.map-wrapper-details > div {
    height: 90%;
    width: 89%;
    position: absolute;
    max-width: 900px;
    margin: 15px 0 30px 3%;
}

.fas.fa-long-arrow-alt-left {
    font-size: 1.7rem;
    cursor: pointer;
    padding: 5px;
    &:hover {
        color: $green;
      }
}
.see-members {
    cursor: pointer;
    .fas.fa-users{
        border: 2px solid white;
        background-color: $bg-white;
        cursor: pointer;
        padding: 5px;
        font-size: 1.4rem;
        &:hover {
            color: $green;
        }
    }
}

@media (max-width: 550px) {
    .fas.fa-long-arrow-alt-left {
        font-size: 1.5rem;
        cursor: pointer;
        margin-bottom: -10px;
    }
    .trip-details {
        margin: 0;
    }
    .main-info-container {
        margin: 10px;
    }
    .title-actions-container, .more-info-container {
        margin: 10px;
    }
    .itinerary-container {
        margin: 10px;
    }
    .img-container {
        margin:0;
        width: 100%;
        .trip-img {
            box-shadow: none;
        }
    }
    .map-wrapper-details > div {
        width: 100%;
        margin: 0;
        padding-top: 10px;
        background-color: $white-trans;
    }
}


